.carousel
    .carousel-inner
        @media ( min-width: 768px )
            height: 400px
        @media ( min-width: 975px )
            height: 450px
        .head-logo
            position: absolute
            z-index: 4
            top: 0
            bottom: 0
            left: 0
            right: 0
            display: flex
            flex-direction: column
            justify-content: center
            align-items: center
            background: rgba(0,0,0,0.6)
            h1
                font-size: 2rem
                color: #fff
                letter-spacing: .4rem
                @media ( min-width: 768px )
                    font-size: 8rem
                    letter-spacing: 0.8rem
                @media ( min-width: 975px )
                    font-size: 10rem
                    letter-spacing: 1rem
            h3 
                color: #fff
                font-size: 1rem
                letter-spacing: .4rem
                @media (min-width: 768px)
                    font-size: 2rem
                    letter-spacing: 0.8rem
                @media ( min-width: 975px )
                    font-size: 2.5rem
                    letter-spacing: 1rem
                    